.header {
  width: 100%;
  height: 80px;
  background-color: rgb(40, 40, 40);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px 0 30px;
  position: fixed;
  top: 0;
  z-index: 999;
}

.logo {
  cursor: pointer;
}

.menu_list {
  display: flex;
  flex-direction: row;
  cursor: default;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu_item {
  margin: 0 15px;
  width: 110px;
  text-align: center;
  font-size: var(--font-size-normal);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu_item_title {
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.login_container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.login {
  margin-right: 30px;
  cursor: pointer;
}
.signup {
  cursor: pointer;
}

.drop_down_menu {
  display: none;
  margin: 0;
  width: 140px;
  position: absolute;
  top: 80px;
  background-color: rgb(40, 40, 40);
  padding: 10px;
  border-top: 3px solid lightgray;
  border-radius: 0 0 4px 4px;
  animation: appear 0.3s;
  list-style: none;
}

.on {
  display: block;
}

.drop_down_menu_item {
  margin: 24px 0;
  cursor: pointer;
}

.drop_down_menu_item:hover {
  color: skyblue;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 780px) {
  .header {
    width: 100vw;
    height: auto;
    flex-direction: column;
  }

  .logo_container {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .menu_list {
    flex-direction: column;
    display: none;
  }

  .login_container {
    display: none;
  }
}
